<template>
	<div class="controls">
		<slot name="controls">
			<div class="previous" @click="slider.showImage('previous')"></div>
			<div :class="autoplayClass" @click="slider.toggleAutoplay()"></div>
			<div class="next" @click="slider.showImage('next')"></div>
		</slot>
	</div>
</template>

<script>
	export default {
		props: {
			slider: { type: Object, required: true }
		},

		computed: {
			controls: function() {
				return this;
			},

			autoplayClass: function() {
				return this.slider.config.autoplay? 'pause' : 'play';
			}
		}
	};
</script>

<style lang="scss" scoped>
	.controls {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 100;
		opacity: 0;
		transition: opacity 0.3s ease-in;
	}

	.controls:hover {
		opacity: 1;
	}

	.previous,
	.play,
	.pause,
	.next {
		position: absolute;
		top: 50%;
		margin-top: -25px;
		width: 50px;
		height: 50px;
		cursor: pointer;
		border-radius: 50%;
		background-color: rgba(0, 0, 0, 0.6);
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 16px 16px;
		transition: background-color 0.2s ease-in;
	}

	.previous {
		left: 25px;
		background-image: url(data:image/png;base64,);
	}

	.play {
		left: 50%;
		margin-left: -25px;
		background-image: url(data:image/png;base64,);
	}

	.pause {
		left: 50%;
		margin-left: -25px;
		background-image: url(data:image/png;base64,);
	}

	.next {
		right: 25px;
		background-image: url(data:image/png;base64,);
	}

	.previous:hover,
	.play:hover,
	.pause:hover,
	.next:hover {
		background-color: rgba(0, 0, 0, 0.9);
	}
</style>
